import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
let _this,timer;
export default {
	components:{
		uniLoadMore
	},
	filters:{
		returnNumber:function(e){
			return e.toFixed(4)
		}
		
	},
	data() {
		return {
			loadingType:'more',
			dataIndex: 1,
			pageNum: 1,
			pageSize: 20,
			items:[],
			userDetail:''
		};
	},
	onReachBottom:function(){
		 if (timer != null) {
		            clearTimeout(timer);
		        }
		   timer = setTimeout(function() {
		        _this.fetchList();
		        }, 1000);
	},
	onLoad:function(){
		_this = this
		this.fetchList()
	},
	methods:{
		async fetchList(){
			if (this.loadingType == ''){
				return
			}
			let data ={
				userCode: '1111',	//用户编码。查看本用户的时候，随便传个值。否则传空字符串
				pageNum: this.pageNum,
				pageSize: this.pageSize
			}
			if (this.dataIndex ==1){
				const res = await this.$http.get('/udeal/buy_list',{data: data})
				if (res.data.data.isLastPage){
					this.loadingType = ''
				}else {
					this.pageSize += 20
				}
				this.items = res.data.data.list
			}else {
				const res = await this.$http.get('/udeal/sell_list',{data: data})
				if (res.data.data.isLastPage){
					this.loadingType = ''
				}else {
					this.pageSize += 20
				}
				this.items = res.data.data.list
			}

		},
		async delOrder(orderCode){
			
			uni.showModal({ 
				title: "友情提示",  
				content: '確認要取消訂單嗎？',  
				success:  async (res) => {  
					if (res.confirm) {  
						let data ={
							orderCode: orderCode,		//买/卖单编码
							buyOrSell: this.dataIndex		//1买，2卖
						}
						const res = await this.$http.get('/udeal/cancel_order',{data: data})
						this.loadingType = 'more'
						this.fetchList()
						
						
					}	
				}  
			})  
			
		},
		selectLabel:function(e){
			this.dataIndex = e.target.dataset.index
			this.pageSize = 20
			this.loadingType = 'more'
			this.fetchList()
			
		},
		
	}
	
}
